.resetpass {
  margin-top: 24vh;
}

.reset-block {
  margin: auto;
  width: 80%;
}

@media only screen and (max-width: 1000px) {
  .reset-block {
    margin: auto;
    width: 100%;
  }
}

@media only screen and (max-width: 800px) {
  .reset-block {
    width: 100%;
  }
  .resetpass {
    margin-top: 18vh;
  }
}
